import React, { useState } from "react";
import Header from "./composant/header.jsx";
import AddAnnonce from "./AddAnnonce.jsx";
import ListAnnonce from "./ListAnnonce.jsx";
import Welcome from "./Welcome.jsx";
import UpdateAnnonce from "./UpdateAnnonce.jsx";
import Contact from "./Contact.jsx";
import Profil from "./profil.jsx";

const Annonce = () => {
    const [toggleState, setToggleState] = useState(1);
    const toggleTab = (index) => {
        setToggleState(index);
    };

    const list = document.querySelectorAll(".list");
    function activeLink() {
        list.forEach((item) => item.classList.remove("active"));
        this.classList.add("active");
    }
    list.forEach((item) => item.addEventListener("click", activeLink));

    return (
        <div>
            <div
                className={
                    toggleState === 1 ? "content content-active" : "content"
                }
            >
                <Header titre="Homepage" sousTitre="Bienvenue!" />
                <Welcome />
            </div>
            <div
                className={
                    toggleState === 2 ? "content content-active" : "content"
                }
            >
                <Header titre="BLUBLOD" sousTitre="Nous contacter" />
                <Contact />
            </div>
            <div
                className={
                    toggleState === 3 ? "content content-active" : "content"
                }
            >
                <Header titre="BLUBLOD" sousTitre="MES ANNONCES" />
                <UpdateAnnonce />
            </div>
            <div
                className={
                    toggleState === 4 ? "content content-active" : "content"
                }
            >
                <Header titre="BLUBLOD" sousTitre="MES ANNONCES" />
                <ListAnnonce />
            </div>
            <div
                className={
                    toggleState === 5 ? "content content-active" : "content"
                }
            >
                <Header titre=" BLUBLOD" sousTitre="MON PROFIL" />
                <Profil />
            </div>

            <div className="conten">
                <div className="navigation">
                    <ul>
                        <li className="list active">
                            <div className="a" onClick={() => toggleTab(1)}>
                                <span className="icon">
                                    <ion-icon name="home-outline"></ion-icon>{" "}
                                </span>
                                <span className="text">Home</span>
                            </div>
                        </li>
                        <li className="list">
                            <div className="a" onClick={() => toggleTab(2)}>
                                <span className="icon">
                                    <ion-icon name="chatbox-outline"></ion-icon>
                                </span>
                                <span className="text">Nous contacter</span>
                            </div>
                        </li>
                        <li className="list">
                            <div className="a" onClick={() => toggleTab(3)}>
                                <span className="icon">
                                    <ion-icon name="duplicate-outline"></ion-icon>
                                </span>
                                <span className="text">Ajouter</span>
                            </div>
                        </li>
                        <li className="list">
                            <div className="a" onClick={() => toggleTab(4)}>
                                <span className="icon">
                                    <ion-icon name="folder-open-outline"></ion-icon>
                                </span>
                                <span className="text">Mes Annonces</span>
                            </div>
                        </li>
                        <li className="list">
                            <div className="a" onClick={() => toggleTab(5)}>
                                <span className="icon">
                                    <ion-icon name="accessibility-outline"></ion-icon>
                                </span>
                                <span className="text">Mon compte</span>
                            </div>
                        </li>
                        <div className="indicator"></div>
                    </ul>
                </div>
            </div>
        </div>
    );
};

export default Annonce;
